html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: black;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.btn {
  position: relative;
  width: max(75px, 5vw);
  height: max(75px, 5vw);
  /* width: 75px;
  height: 75px; */
}
.btn.hovered .btn_bg {
  fill: white;
}
.btn_bg {
  transition: 0.4s fill;
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  fill: none;
  width: max(50px, 3vw);
  height: max(50px, 3vw);
}
.btn_bg path {
  stroke: white;
  stroke-width: 0.5px;
  stroke-linejoin: round;
}
.btn.hovered .btn_bg path:last-child {
  fill: black;
}
.btn.hovered .btn_bg path:first-child {
  transform-origin: 50% 50%;
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}
@-moz-keyframes spin {
  100% {
    -moz-transform: rotateZ(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotateZ(360deg);
  }
}
@keyframes spin {
  100% {
    transform: rotateZ(360deg);
  }
}
.controller {
  position: relative;
  top: -3vw;
}

.header {
  position: absolute;
  top: 0;
  color: white;
  background: none;
  z-index: 1;
  pointer-events: none;
  left: 10%;
}
.heading-container {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* top: 0; */
  bottom: 0;
  color: white;
  width: 100vw;
}
.lists {
  position: relative;
  top: -3vw;
  width: 100%;
  height: 100%;
}
ul {
  left: 10%;
  position: absolute;
  /* position: relative; */
  height: 8vw;
  width: 100%;
  overflow: hidden;
  list-style: none;
  margin: 0;
}
.outline {
  top: -8vw;
}
.dark {
  top: 0vw;
}
/* .dark{
  transform: translateY(1vw);
} */
li {
  font-size: 6vw;
  font-weight: bolder;
  position: absolute;
  user-select: none;
}
hr {
  position: absolute;
  width: 100vw;
  /* margin-top: 1vw; */
  margin: 0;
  opacity: 0.1;
}
.outline {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(172, 172, 172);
}
.outline li {
  top: 13vw;
}
.dark li {
  top: -8vw;
}
/* .outline li:first-child {
  top: 0vw;
} */
/* .dark li:first-child {
  top: 0;
} */
.inactive{
  opacity: 0;
}
.invisible {
  opacity: 0;
}
.outline li.active {
  animation: slide1 1s ease-out forwards;
}
.outline li.inactive {
  animation: slide11 0.3s cubic-bezier(0.24, 1.22, 1, 1) forwards;
}
.dark li.active {
  animation: slide2 1s cubic-bezier(0.24, 1.22, 1, 1) forwards;
}
.dark li.inactive {
  animation: slide22 1s cubic-bezier(0.24, 1.22, 1, 1) forwards;
}
@keyframes slide1 {
  0% {
    transform: skewY(17deg);
    top: 13vw;
  }
  100% {
    transform: skewY(0px);
    top: 0vw;
  }
}
@keyframes slide11 {
  0% {
    transform: skewY(0px);
    top: 0vw;
  }
  50% {
    transform: skewY(10deg);
  }
  100% {
    transform: skewY(0px);
    top: 8vw;
  }
}
@keyframes slide2 {
  0% {
    transform: skewY(-17deg);
    top: -15vw;
  }
  100% {
    transform: skewY(0px);
    top: 0vw;
  }
}
@keyframes slide22 {
  0% {
    transform: skewY(0deg);
    top: 0vw;
  }
  50% {
    transform: skewY(-10deg);
  }
  100% {
    transform: skewY(0deg);
    top: -15vw;
  }
}
@media only screen and (max-width: 700px) {
  .header {
    left: 5%;
  }
  ul {
    left: -1%;
  }
  li {
    font-size: 10vw;
  }
  .outline{
    /* overflow: visible; */
    top: -13vw;
    height: 13vw;
  }
  .dark{
    height: 14vw;
  }
  .outline li.active {
    animation: slide1r 1s ease-out forwards;
  }
  .outline li.inactive {
    animation: slide11r 0.3s cubic-bezier(0.24, 1.22, 1, 1) forwards;
  }
  .dark li.active {
    animation: slide2r 1s cubic-bezier(0.24, 1.22, 1, 1) forwards;
  }
  .dark li.inactive {
    animation: slide22r 0.5s cubic-bezier(0.24, 1.22, 1, 1) forwards;
  }
}
@keyframes slide1r {
  0% {
    transform: skewY(17deg);
    top: 13vw;
  }
  100% {
    transform: skewY(0px);
    top: 0vw;
  }
}
@keyframes slide11r {
  0% {
    transform: skewY(0px);
    top: 0vw;
    opacity: 1;
  }
  50% {
    transform: skewY(10deg);
    opacity: 0;
  }
  100% {
    transform: skewY(0px);
    top: 13vw;
  }
}
@keyframes slide2r {
  0% {
    transform: skewY(-17deg);
    top: -15vw;
  }
  100% {
    transform: skewY(0px);
    top: 2vw;
  }
}
@keyframes slide22r {
  0% {
    transform: skewY(0deg);
    top: 0vw;
    opacity: 1;
  }
  50% {
    transform: skewY(-10deg);
    opacity:0;
  }
  100% {
    transform: skewY(0deg);
    top: -15vw;
  }
}
